*,
*::before,
*::after {
  box-sizing: border-box;
}

.flex {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}

.--just-space {
  justify-content: space-between;
}

.--just-center {
  justify-content: center;
}

.--just-end {
  justify-content: flex-end;
}

.--align-str {
  align-items: stretch;
}

.--align-center {
  align-items: center;
}

.--align-end {
  align-items: flex-end;
}

.--dir-col {
  flex-direction: column;
}

html {
  font-size: 16px;
}

html,
body {
  min-height: 100%;
  position: relative;
}

body {
  min-height: 100vh;
  font-size: 1rem;
  line-height: 1.25;
  font-family: "Avenir Next", Arial, Helvetica, sans-serif;
  background: #f7f7f7;

  &.dark {
    color: #ffffff;
    background-color: #1F1D2B;
  }
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

.container {
  max-width: 120rem;
  width: 100%;
  margin: 0 auto;
  padding: 0 2rem;

  &.--1000 {
    max-width: 64.5rem;
  }

  &.--720 {
    max-width: 47rem;
  }

  &.--600 {
    max-width: 40rem;
  }

  &.--392 {
    max-width: 26.5rem;
  }
}

.page-wrapper {
  min-height: 100vh;

  header,
  main,
  footer {
    width: 100%;
  }

  header,
  footer {
    flex: 0 0 auto;
  }

  main {
    flex: 1 1 100%;
  }
}